const axios = require('axios');
const baseUrl = '/api/reset_pw'

const forgot = async (email) => {
    const result = axios.post(`${baseUrl}/forgot_auth`, email);
    return result.data;
}

const reset = async (token) => {
    const result = axios.get(`${baseUrl}/reset_auth/${token}`);
    return result.data;
}

export default { forgot, reset };